.navbar {
  position: sticky;
  top: 0;
  background-color: rgba(250, 250, 250, 0.306,5); 
  padding: 10px 0;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navbar ul li {
  display: inline;
  margin-right: 20px;
}

.navbar ul li a {
  color: #010101;
  text-decoration: none;
}
